.description {
  font-size: 14px;
}

.input {
  width: 100%;
  border: none;
  border-bottom: 2px solid lightgreen;
  padding: 6px;
  transition: all 0.3s linear;
  outline: none;
  font-size: 20px;
  margin-bottom: 20px;
}

.input:focus,
.input:hover {
  border-bottom: 2px solid green;
}
